<template>
  <div>
    <el-tabs v-model="defaultYear" type="card">
      <el-tab-pane :label="`${c.CurrentYear}`" :name="c.CurrentYear">
        <div style="display:inline-block;width:60%;vertical-align: top;">
          <div class="top-add">
            <el-button @click="handleAdd" type="primary" plain small></el-button>
          </div>
          <div class="top-container">
            <el-tag type="success">11</el-tag>
          </div>
        </div>
        <div style="display:inline-block;text-align:left;width:35%">
          <el-card class="box-card">
            <div style="color: red;font-size: 16px;font-weight: 700"></div>
            <div>1.</div>
            <div>2.</div>
            <div>3.</div>
            <div>4.</div>
            <div style="color: red">:""""</div>
          </el-card>
        </div>
        <el-dialog
          title=""
          :visible.sync="dialogVisible"
        >
          <el-form :model="formData" ref="formData">
            <el-form-item v-show="false">
              <el-input auto-complete="off" v-model="formData.id"></el-input>
            </el-form-item>
            <el-form-item label=":" >
              <el-select placeholder="" v-model="formData.level">
                <el-option :label="levels[1]" value="1"></el-option>
                <el-option :label="levels[2]" value="2"></el-option>
                <el-option :label="levels[3]" value="3"></el-option>
              </el-select>
            </el-form-item>
            <!-- <el-form-item
              label=":"
              :rules="[
                { required: true, message: ''}
              ]"
              prop="areaName">
              <el-input 
                placeholder="XXXX"
                auto-complete="off" v-model="formData.areaName"></el-input>
            </el-form-item> -->
            <el-form-item
              label=":"
              
              prop="shtt"
              :rules="[
                { required: true, message: ''},
                { type: 'number', message: ''}
              ]">
              <el-input auto-complete="off" v-model.number="formData.shtt"></el-input>
            </el-form-item>
            <el-form-item
              label=":"
              prop="shfwjg"
              
              :rules="[
                { required: true, message: ''},
                { type: 'number', message: ''}
              ]">
              <el-input auto-complete="off" v-model.number="formData.shfwjg"></el-input>
            </el-form-item>
            <el-form-item
              label=":"
              prop="jjh"
              
              :rules="[
                { required: true, message: ''},
                { type: 'number', message: ''}
              ]">
              <el-input auto-complete="off" v-model.number="formData.jjh"></el-input>
            </el-form-item>
            <el-form-item
              label=":"
              prop="innerOrgNum"
              
              :rules="[
                { required: true, message: ''},
                { type: 'number', message: ''}
              ]">
              <el-input
                auto-complete="off" v-model.number="formData.innerOrgNum"></el-input>
            </el-form-item>
            <el-form-item
              label=":"
              prop="sqTotalNum"
              :rules="[
                { type: 'number', message: ''}
              ]">
              <el-input 
                placeholder=""
                auto-complete="off" v-model.number="formData.sqTotalNum"></el-input>
            </el-form-item>
            <el-form-item
              label=":"
              prop="totalNum"
              
              :rules="[
                { type: 'number', message: ''}
              ]">
              <el-input 
                placeholder=""
                auto-complete="off" v-model.number="formData.totalNum"></el-input>
            </el-form-item>
            <el-form-item label=":">
              <el-upload
                action="/file_upload"
                :data="{'menu': 'table11'}"
                :on-remove="handleRemove"
                accept=".doc, .docx, .pdf"
                :on-success="uploadSuccess"
                :limit="1"
                :on-exceed="handleExceed"
                :file-list="formData.fileList">
                <el-button size="small" type="primary"></el-button>
                <div slot="tip" class="el-upload__tip">doc/docx/pdf</div>
              </el-upload>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="confirmAdd" v-if="action=='add'"> </el-button>
            <el-button type="primary" @click="confirmEdit" v-if="action=='edit'"> </el-button>
          </div>
        </el-dialog>
        <el-table
        :data="tableData"
        border
        show-summary
        style="width: 95%;margin-left:20px">
          <el-table-column
            width="50"
            label="">
            <template slot-scope="scope">{{ scope.$index + 1 }}</template>
          </el-table-column>
          <el-table-column
            prop="province"
            sortable
            label="">
          </el-table-column>
          <el-table-column
            sortable
            label="">
            <template slot-scope="scope">
              {{levels[scope.row.level]}}
            </template>
          </el-table-column>
          <!-- <el-table-column
            prop="areaName"
            sortable
            label="">
          </el-table-column> -->
          <el-table-column label="">
            <el-table-column
              prop="shtt"
              sortable
              label="">
            </el-table-column>
            <el-table-column
              prop="shfwjg"
              sortable
              label="">
            </el-table-column>
            <el-table-column
              prop="jjh"
              sortable
              label="">
            </el-table-column>
          </el-table-column>
          <el-table-column
            prop="innerOrgNum"
            sortable
            label="">
          </el-table-column>
          <el-table-column
            prop="sqTotalNum"
            sortable
            label="">
          </el-table-column>
          <el-table-column
            prop="totalNum"
            sortable
            label="">
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-card class="box-card" style="width: 80%; text-align: left; margin:20px 0 0 20px; font-size: 16px">
          <div slot="header" class="clearfix">
            
          </div>
          <ol>
            <li v-for="file in fileLists">
              {{file}}
            </li>
          </ol>
        </el-card>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import axios from 'axios';
import provinces from '../../config/provinces'
import utils from '../../utils'
import c from '$config/current'
import qs from 'qs'


export default {
  data () {
    return {
      tableData: [],
      c,
      formData: {
        id: null,
        province: null,
        level: null,
        // areaName: null,
        shtt: null,
        shfwjg: null,
        jjh: null,
        innerOrgNum: null,
        totalNum: null,
        fileList: []
      },
      fileLists: [],
      utils: utils,
      dialogVisible: false,
      action: 'add',
      defaultYear: '2019',
      levels: ['0', '', '', ''],
      provinces: provinces.provinces
    }
  },
  mounted () {
    this.getData();
  },
  methods: {
    handleExceed(file, fileList) {
      this.$message.warning('!');
      console.log(file, fileList);
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    uploadSuccess(file, fileList) {
      console.log(file, fileList)
    },
    getData () {
      let params = {};
      let config = {
        // apibaseURL, DataSourceGateWay
        baseURL: '/api/v1',
        url: '/zyfwzuzhi/get',
        // to methods of that instance.
        method: 'get',
        // paramsget url
        params: params
      };
      axios.request(config)
      .then(result => {
          let data = result.data;
        console.log(data);
        this.tableData = data.data;
        if(data.fileList) {
          // debugger
          this.fileLists = data.fileList;
        }
      }, err => {
        this.$alert(err.message);
      });
    },
    resetForm () {
      this.formData = {
        id: null,
        province: null,
        level: null,
        // areaName: null,
        shtt: null,
        shfwjg: null,
        jjh: null,
        innerOrgNum: null,
        totalNum: null,
        fileList: []
      }
    },
    handleEdit (index, rowData) {
      console.log(index, rowData, this.formData)
      this.resetForm();
      this.action = 'edit';
      Object.keys(rowData).forEach(item => {
        this.formData[item] = rowData[item];
      })
      this.dialogVisible = true;
    },
    handleDelete (index, rowData) {
      console.log(index, rowData)
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let config = {
          baseURL: '/api/v1',
          paramSerializerJQLikeEnabled: true,
          url: '/zyfwzuzhi/delete',
          method: 'post',
          data: qs.stringify({id: rowData.id})
        };
        axios.request(config)
        .then(result => {
          let data = result.data;
          if (Object.keys(data) == 0) {
            this.$message({
              message: '',
              type: 'success'
            });
            this.tableData.splice(index, 1);
          } else {
            this.$message({
              message: '',
              type: 'error'
            });
          }
        }, err => {
          this.$alert(err.message);
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });          
      });
    },
    handleAdd () {
      this.resetForm();
      this.action = 'add';
      this.dialogVisible = true;
    },
    confirmAdd () {
      let data = this.formData;
      let config = {
        // apibaseURL, DataSourceGateWay
        baseURL: '/api/v1',
        url: '/zyfwzuzhi/add',
        // to methods of that instance.
        method: 'post',
        // datapost http
        data: qs.stringify(data)
      };
       this.$refs.formData.validate((valid) => {
        if (valid) {
          axios.request(config)
          .then(result => {
          let data = result.data;
            console.log(data);
            if (data.id) {
              this.$message({
                message: '',
                type: 'success'
              });
              this.formData.id = data.id;
              this.formData.province = data.province;
              this.tableData.unshift(this.formData);
              this.dialogVisible = false;
            } else {
              this.$message({
                message: '',
                type: 'error'
              });
            }
          }, err => {
            this.$alert(err.message);
          });
        }
      })
    },
    confirmEdit () {
      let data = this.formData;
      let config = {
        baseURL: '/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/zyfwzuzhi/update',
        method: 'post',
        data: qs.stringify(data)
      };
      axios.request(config)
      .then(result => {
          let data = result.data;
        if (Object.keys(data) == 0) {
          this.$message({
            message: '',
            type: 'success'
          });
          this.tableData.forEach((item, index) => {
            if (this.formData.id == item.id) {
              this.tableData.splice(index, 1, this.formData);
            }
          })
          console.log(this.tableData);
          this.dialogVisible = false;
        } else {
          this.$message({
            message: '',
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    }
  }
}

</script>
<style lang="less">
  .dialog-footer {
    text-align:center;
  }
</style>


